.section {
    margin-bottom: 40px;
  }
  
  .section-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 8px;
  }
  
  .section-title {
    font-size: 20px;
    color: #333;
  }
  
  .dropdown {
    display: flex;
    align-items: center;
    gap: 8px;
  }
  
  .dropdown-select {
    padding: 4px 8px;
    font-size: 14px;
    border: 1px solid #ccc;
    border-radius: 4px;
    background-color: #fff;
    cursor: pointer;
  }
  
  .section-description {
    font-size: 14px;
    color: #666;
    margin-bottom: 16px;
  }
  
  .card-container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 16px;
  }
  